.select {
  box-shadow: 12px 15px 10px -5px #718b93;
  transition: box-shadow ease-in-out 0.2s, left ease-in-out 0.2s;
}

.select.select-with-focus,
.select:hover {
  outline: none;

  box-shadow: 0px 0px 0px 0px #718b93;
}

.select-value {
  display: flex;
  align-items: center;
}

.select-value-text {
  margin-left: 10px;
  font-weight: bold;
}


.select-option-item {
  display: flex;
  align-items: center;
}

.select-option-item-text {
  margin-left: 10px;
}
